@import '../../style/style.scss';


.item-group-container-underline {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    align-items: stretch;
    flex-wrap: wrap;
    padding: 0px;
    gap: 40px;


    .item-group-item-underline {
        display: flex;
        gap: 8px;
        flex-direction: column;
        width: 100%;

        @media (min-width: $screen-s-min) {
            flex: 1 0 40%;
            /* explanation below */
        }

        @media (min-width: $screen-l-min) {
            flex: 1 0 35%;
            /* explanation below */
        }

        .item-group-header {
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 100%;
            align-items: flex-start;
            padding: 0px;
            gap: 4px;

            .item-group-header-underline {
                border: 1px solid $primaryMain;
                flex: none;
                order: 1;
                align-self: stretch;
                flex-grow: 0;

            }

            .item-group-headline {
                display: flex;
                flex-direction: row;
                gap: 8px;
            }

        }

    }
}

.item-group-container-box {

    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    align-items: stretch;
    flex-wrap: wrap;
    padding: 0px;
    gap: 24px;

    .item-group-item-box {

        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 32px;
        gap: 8px;
        background: $greyBackground;
        box-shadow: $boxShadow;
        align-self: stretch;
    }

}

.item-group-item-box-small {
    width: 100%;

    @media (min-width: $screen-s-min) {
        flex: 1 0 30%;
        /* explanation below */
    }

    @media (min-width: $screen-l-min) {
        flex: 1 0 5%;
        /* explanation below */
    }
}

.item-group-item-box-large {

    @media (min-width: $screen-m-min) {
        flex: 1 0 30%;
        /* explanation below */
    }
}